.additional__container {
  width: 417px;
  height: 80px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  padding: 0px;
  margin: 48px 0px;
  opacity: 0.5;
}

.additional__text {
  position: static;
  width: 417px;
  height: 24px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  padding: 0px;
  margin: 24px 0px;
  color: rgb(0, 0, 0);
  font-family: Roboto;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0%;
  text-align: left;
}

.additional__btn {
  width: 120px;
  height: 32px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 4px 15px 4px 15px;
  cursor: pointer;
  color: rgba(0, 0, 0, 0.85);
  font-family: Roboto;
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0%;
  text-align: center;
  border: 1px solid rgb(217, 217, 217);
  border-radius: 2px;
  box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.02);
  background: rgb(255, 255, 255);
}

.additional__btn:active {
  background-color: rgba(128, 128, 128, 0.096);
}

.additional__btn:hover {
  color: black;
  border: 1px solid rgb(0, 0, 0);
}

.link {
  color: inherit;
  text-decoration: none;
}

@media (max-width: 1900px) {
  .additional__container {
    width: auto;
    margin: 0;
    justify-content: flex-start;
    height: auto;
  }
  .additional__text {
    padding: 8px;
    margin: 0;
    width: auto;
    height: auto;
  }
}
