.dropDown__title {
  position: static;
  width: 120px;
  height: 24px;

  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  padding: 0px;
  color: rgb(0, 0, 0);
  font-family: Roboto;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 0%;
  text-align: right;
}

@media (max-width: 1900px) {
  .dropDown__title {
    width: auto;
  }
}
